<script>
import Layout from '@layouts/main'
import PageHeader from '@components/page-header'
import axios from 'axios';
import Utils from '../../../../../utils/Util'

/**
 * Invoice component
 */
export default {
	page: {
		title: 'Sertifikat',
	},
	components: { Layout, PageHeader },
	data() {
		return {
			title: 'Sertifikat',
			api: Utils.Host(),
			message: "",
			data:[],
			options: "",
			alert:false,
			datatampil: [],
			modelpage:"1",
			modellimit: "10",
			datatampil: [],
			modelsearch: "",
		}
	},
	mounted() {
      console.log("alamat", Utils.Host())
      this.Getdata()
    },
	computed: {
	  pilihmodelpage() {
        return this.Getdata(this.modelpage);
      },
      pilihmodellimit() {
        return this.Getdata(this.modelpage, this.modellimit);
      },
	  pilihmodelsearch() {
        return this.Getdata(this.modelpage, this.modellimit, this.modelsearch);
      },
    },
	methods: {
      async Getdata(page,limit,search) {
        try {
			let kodepage = page ? page : 1;
			let kodelimit = limit ? limit : 10;
            let kodesearch = search ? search : "";
            let response = await axios.get(this.api+"/sertifikat?search="+kodesearch+
            "&limit="+
            kodelimit+
            "&page="+
            kodepage, Utils.authHeader());
            this.data = response.data.Data;
        } catch (err) {
            console.error(err);
        }
      },
      async openEdit(id) {
          document.location = '/sertifikataction/edit/' + id
      },
      async openDetail(id) {
          document.location = '/sertifikataction/detail/' + id
      },
      async openRemove(id) {
		if (confirm('Anda Yakin akan menghapus Data')) {
		const item = this.data.find((el) => el.f_sertifikat_id === id);
        const res =  await axios.delete(this.api+"/sertifikat/"+item.f_sertifikat_id, Utils.authHeader());
		this.alert = true
        this.message = res.data.Error_desc
        this.Getdata()
		} else {
        
		}
	  }
	}
}
</script>

<template>
	<Layout>
		<PageHeader :title="title" :items="items" class="d-print-none" />
		<div class="row">
			<div class="col-12">
				<div class="card">
					<div class="card-body">
						<!-- Logo & title -->
						
						<div class="row">
							<div class="col-12">
								<div class="table-responsive">
									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Halaman"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="modelpage"
										type="number"
										required
										@click="pilihmodelpage"
									></b-form-input>
									</b-form-group>
									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Jumlah Tampil"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="modellimit"
										type="number"
										required
										@click="pilihmodellimit"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Nama"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="modelsearch"
										type="text"
										required
										@click="pilihmodelsearch"
									></b-form-input>
									</b-form-group>
									<br>&nbsp;&nbsp;
									<b-button variant="dark" to="/sertifikataction/tambah">Tambah Data</b-button>
									<br>
									<b-alert
											v-model="alert"
											variant="success"
											dismissible
											>{{ message }}</b-alert
										>
									<br>
									<div class="table-responsive">
									<table class="table mb-0">
										<thead>
										<tr>
											<th scope="col">No</th>
											<th scope="col">Tahun</th>
											<th scope="col">Pencarian</th>
											<th scope="col">Nama</th>
											<th scope="col">Status</th>
											<th scope="col">Nomor</th>
											<th scope="col">Tanggal Buat</th>
											<th scope="col">Tanggal Update</th>
											<th scope="col">Action</th>
										</tr>
										</thead>
										<tbody>
										<tr v-for="(tampil,index) in data" :key="tampil.f_sertifikat_id">
											<th scope="row">{{index + 1}}</th>
											<td>{{tampil.f_tahun}}</td>
											<td>{{tampil.f_pencarian}}</td>
											<td>{{tampil.f_nama}}</td>
											<td>{{tampil.f_status}}</td>
											<td>{{tampil.f_nomor}}</td>
											<td>{{tampil.f_tgl_create}}</td>
											<td>{{tampil.f_last_update}}</td>
											<td>
											<b-button variant="info" @click="openDetail(tampil.f_sertifikat_id)">Detail Data</b-button>
											&nbsp;
											<b-button variant="warning" @click="openEdit(tampil.f_sertifikat_id)">Edit Data</b-button>
											&nbsp;
											<b-button variant="danger" @click="openRemove(tampil.f_sertifikat_id)">Delete Data</b-button>
											</td>
										</tr>
										</tbody>
									</table>
									</div>

								</div>
								<!-- end table-responsive -->
							</div>
							<!-- end col -->
						</div>
						<!-- end row -->

	
					</div>
				</div>
			</div>
			<!-- end col -->
		</div>
		<!-- end row -->
		<!-- end row -->
	</Layout>
</template>
